.cards-news,
.other-news {
  background-color: var(--light-color);
  padding: 30px 0;

  @media (max-width: 1330px) {
    padding: 20px 0;
  }

  &__inner {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-gap: 30px;
    margin-bottom: 30px;

    @media (max-width: 1330px) {
      grid-gap: 20px;
      margin-bottom: 20px;
    }
    @media (max-width: 767px) {
      grid-template-columns: repeat(3, 1fr);
      grid-gap: 10px;
    }
    @media (max-width: 525px) {
      grid-template-columns: repeat(2, 1fr);
      margin-bottom: 10px;
    }
  }

  &__item {
    background-color: #fff;
    height: 383px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    @media (max-width: 1330px) {
      height: 260px;
    }
    @media (max-width: 525px) {
      height: 193px;
    }
  }

  &__image {
    background-color: var(--primary-color);
    max-width: 100%;

    img,
    source {
      width: 100%;
      height: 224px;
      object-fit: cover;

      @media (max-width: 1330px) {
        height: 164px;
      }
      @media (max-width: 525px) {
        height: 100px;
      }
    }
  }

  &__content {
    padding: 21px 20px 27px 20px;

    @media (max-width: 1330px) {
      padding: 15px 10px 18px 10px;
    }
    @media (max-width: 767px) {
      padding: 6px 10px 18px 8px;
    }

    a {
      font-weight: 700;
      font-size: 16px;
      line-height: 140%;
      letter-spacing: 0.02em;
      color: var(--secondary-color);
      padding-right: 10px;
      position: relative;

      @media (max-width: 1330px) {
        font-size: 12px;
        padding-right: 5px;
      }

      .read__more {
        fill: var(--secondary-color);
      }
    }
  }

  &__title {
    font-weight: 400;
    font-size: 16px;
    line-height: 150%;
    letter-spacing: 0.02em;
    color: var(--text-color-dark);
    margin-bottom: 15px;

    /*Ограничение по кол-ву строк*/
    display: -webkit-box;
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;
    overflow: hidden;
    text-overflow: ellipsis;

    @media (max-width: 1330px) {
      -webkit-line-clamp: 2;
      font-size: 12px;
      margin-bottom: 11px;
    }
    @media (max-width: 767px) {
      -webkit-line-clamp: 3;
      font-size: 11px;
      margin-bottom: 4px;
    }
  }

  &__show {
    margin-bottom: 30px;
    background-color: #fff;
    height: 50px;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;

    @media (max-width: 1330px) {
      height: 30px;
      margin-bottom: 20px;
    }
    @media (max-width: 425px) {
      margin-bottom: 10px;
    }
  }

  &__show-link {
    font-weight: 900;
    font-size: 16px;
    line-height: 150%;
    text-align: center;
    letter-spacing: 0.04em;
    text-transform: uppercase;
    color: var(--primary-color);

    @media (max-width: 1330px) {
      font-size: 12px;
    }
    @media (max-width: 767px) {
      font-size: 11px;
      letter-spacing: 0.03em;
    }
  }
}